
a,
a:hover,
a:active,
a:before,
a:after,
a:visited {
  text-decoration: none;
  color: #000;
}
.left_box {
  background-color: #563d7c;
  width: 80px;
  height: 120px;
  position: absolute;
  left: 50px;
  z-index: 1;
}
.right_img {
  position: absolute;
  right: 10px;
  top: 0;
  z-index: 1;
}
nav {
  background: #fff;
  text-align: center;
}
nav ul {
  list-style: none;
  display: inline-block;
  padding: 22px;
  font-size: 0;
}
nav ul li {
  font-size: 20px;
  transition: 0.5s;
  display: inline-block;
  padding: 10px 10px;
  border-bottom: 2px #aaa solid;
}
nav ul input {
  border: 0;
}
nav ul input,
nav ul .logo,
nav ul .nav_toggle {
  display: none;
}
nav ul .normal {
  cursor: pointer;
}
nav ul .normal:hover {
  border-bottom: 2px #572a7b solid;
}
nav ul .normal.router-link-active {
  border-bottom: 2px #572a7b solid;
}
@media only screen and (max-width: 1000px) {
.left_box,
  .right_img {
    display: none;
}
}
@media only screen and (max-width: 768px) {
nav ul {
    padding: 0;
}
nav ul .point {
    display: none;
}
nav ul .normal {
    padding: 0;
    width: 100vw;
    display: block;
    overflow: hidden;
    max-height: 0;
    border-bottom: 0;
}
nav ul .normal.router-link-active {
    border-bottom: 0;
}
nav ul .normal:hover {
    border-bottom: 0;
}
nav ul .nav_toggle,
  nav ul .logo {
    margin: 0;
    padding: 0;
    border-bottom: 0;
    display: inline-block;
}
nav ul .nav_toggle {
    font-size: 25px;
    float: right;
    padding: 0 20px;
    margin-top: 15px;
}
nav ul .logo {
    float: left;
    height: 60px;
}
nav ul .logo img {
    height: 100%;
}
nav ul input,
  nav ul input:before,
  nav ul input:after,
  nav ul input:visited {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}
nav ul li label {
    cursor: pointer;
}
nav ul input + li label:after {
    content: "☰";
}
nav ul input:checked + li label:after {
    content: "☰";
}
nav ul input:checked ~ .normal {
    max-height: 150px;
    padding: 10px 0;
    border-bottom: 1px #aaa solid;
}
nav ul input:checked ~ .normal.router-link-active {
    border-bottom: 1px #572a7b solid;
}
nav ul input:checked ~ .normal:hover {
    border-bottom: 1px #572a7b solid;
}
}


